<template>
  <section class="playlist">
    <div class="playlist__container container">
      <div class="playlist__inner">
        <div class="playlist__items playlist-items">
          <div class="playlist-items__inner">
            <play-list-item 
              v-for='track of getAllTracks' 
              :key='track.id' 
              :track='track'
              @trackPlay = 'play'></play-list-item>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import PlayListItem from '@/components/PlayListItem'
import {mapGetters} from 'vuex'


export default {
  name: "Home",
  data(){
    return {
      audioNodeList: [],
    }
  },
  mounted(){
    this.audioNodeList = document.querySelectorAll('.audio-playlist')
  },
  computed: {
    ...mapGetters(['getAllTracks']),
  },
  components: {
    'play-list-item': PlayListItem,
  },
  methods: {
    play(title){
      for (let i=0;i<this.getAllTracks.length;i++){
        if (this.getAllTracks[i].title !== title){
          this.audioNodeList[i].pause()
        }
      }
    },
  }
};
</script>
