<template>
   <div class="playlist-items__item">
      <div class="playlist-items__row">
         <div 
            class="playlist-items__image" 
            :class="[
               track.title === currentTrackPlay ? 'play' : '',
            ]"
         >
            <img :src="getImageUrl" :alt="`Track name: ${track.title}`">
         </div>
         <div class="playlist-items__title title">
            <h4 class="title__main">{{track.title}}</h4>
            <p class="title__sub">{{track.text}}</p>
         </div>
         <div class="playlist-items__detail-info">
            <div class="playlist-items__date-created">{{track.dateCreated}}</div>
         </div>
         <div class="playlist-items__action">
            <div 
               class="playlist-items__audio"
               :class="[
                  track.title === currentTrackPlay ? 'active' : '',
               ]"
            >
               <audio
                  :src="getTrackUrl"
                  controls
                  class='audio-playlist'
                  @play="trackPlay(track.title)"
                  @pause ="currentTrackPlay = ''"
                  loop
                  ></audio>
            </div>
            <button class="playlist-items__favorite" type="button">
               <img src="@/assets/icons/favorite.svg" :alt="`favorite tracks: ${track.title}`">
            </button>
         </div>
      </div>    
   </div>
</template>

<script>
export default {
   data(){
      return {
         currentTrackPlay: '',
      }
   },
   props: {
      track: Object,
   },
   computed: {
      getImageUrl(){
         return require('@/media/pictures/' + this.$props.track.pictureSource)
      },
      getTrackUrl(){
         return require('@/media/tracks/' + this.$props.track.audioSource)
      }
   },
   methods: {
      trackPlay(title){
         this.currentTrackPlay = title
         this.$emit('trackPlay', title)
      },
   }
}
</script>